











import { Vue, Component, Prop } from 'vue-property-decorator';
import { State } from 'vuex-class';
import RoutePage from '@/core/route';
import OffersContainer from '@/core/offers';
import OfferViewDesktop from '@/components/offer-view-desktop';
import OfferViewMobile from '@/components/offer-view-mobile';

@Component({
  components: {
    OfferViewDesktop,
    OfferViewMobile
  }
})
export default class OfferView extends RoutePage {

  // @Prop(Object) offer: any;
  @Prop(String) id: string;

  @State layout;


  offers: any  = null;

  get offer() {
    return this.offers && this.offers.data && this.offers.data[0];
  }

  buy() {
    console.log('buy')

  }

  created() {

    this.offers = new OffersContainer();

    this.offers.get();

    console.log(this.$route.params);
  }
}



